// HACK(mgnb): this is a template that is copied into .cache by
// gatsby-on-create-node so that React example can be compiled into their own
// webpack modules
import template from '../src/templates/example-page-template';

import reactExample0 from 'C:/projects/bhp-digital-design-system/.cache/ui-components-toggle-switch-react-example-0.js'
import reactExample1 from 'C:/projects/bhp-digital-design-system/.cache/ui-components-toggle-switch-react-example-1.js'

const reactExamples = [
  reactExample0,reactExample1
];

export default ({ data }) => {
  const contents = Object.assign({}, data.uiComponentsYaml);
  contents.examples = contents.examples.map((example, index) => {
    if (example.react) {
      example = Object.assign({}, example);
      example.react = {
        source: example.react,
        fn: reactExamples[index],
      };
    }
    return example;
  });
  return template({
    contents,
    hideCodeTypePicker: false
  });
};

export const query = graphql`
  query uiComponentsToggleSwitch($slug: String!) {
    uiComponentsYaml(fields: { slug: { eq: $slug } }) {
      title
      category
      examples {
        title
        introduction
        subTitle
        react
        angular
        component
        css
        html
        actualHtml
      }
      fields {
        slug
        preHtml
        postHtml
      }
    }
  }
`;



