import React from 'react';
import { ToggleSwitch } from 'bhp-ui-react';

class Example extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      on: [false, true, true, true, true, true]
    };
  }

  toggle(index) {
    this.setState(prevState => {
      prevState.on[index] = !prevState.on[index];
      return prevState;
    });
  }

  render() {
    return (
      <div>
        <ToggleSwitch
          color='primary'
          onMessage='SÍ'
          offMessage='NO'
          on={this.state.on[0]}
          onClick={() => this.toggle(0)}
        />{' '}
        <ToggleSwitch
          color='primary'
          onMessage='SÍ'
          offMessage='NO'
          on={this.state.on[1]}
          onClick={() => this.toggle(1)}
        />{' '}
        <ToggleSwitch
          color='info'
          on={this.state.on[2]}
          onClick={() => this.toggle(2)}
        />{' '}
        <ToggleSwitch
          color='success'
          on={this.state.on[3]}
          onClick={() => this.toggle(3)}
        />{' '}
        <ToggleSwitch
          color='danger'
          on={this.state.on[4]}
          onClick={() => this.toggle(4)}
        />{' '}
        <ToggleSwitch
          color='warning'
          on={this.state.on[5]}
          onClick={() => this.toggle(5)}
        />{' '}
      </div>
    );
  }
}

export default Example;



